.topNavBar {
  padding: 20px 0 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  z-index: 1;
  color: #B7B7B7;
}
.topNavBar.sticky {
  background: white;
  padding: 10px 0;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  color: #3d4451;
}
.topNavBar-inner {
  padding: 0 16px;
  /*display:flex;*/
  /*flex-direction: column;*/
  /*align-items: center;*/
}
.topNavBar-inner .menu {
  float: right;
}
.topNavBar-inner .logo {
  float: left;
}
.topNavBar nav {
  padding-top: 5px;
}
.topNavBar nav > ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.topNavBar nav > ul > li {
  float: left;
  margin-left: 30px;
  margin-right: 30px;
  position: relative;
}
.topNavBar nav > ul > li a {
  font-size: 18px;
  color: inherit;
  text-decoration: none;
  font-weight: bold;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  padding-top: 5px;
  padding-bottom: 5px;
  display: block;
  position: relative;
}
.topNavBar nav > ul > li.active > a::after,
.topNavBar nav > ul > li.highlight > a::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: rgb(252, 110, 146);
  height: 3px;
  -webkit-animation: menuSlide 0.3s;
          animation: menuSlide 0.3s;
}
@-webkit-keyframes menuSlide {
0% {
    width: 0;
}
100% {
    width: 100%;
}
}
@keyframes menuSlide {
0% {
    width: 0;
}
100% {
    width: 100%;
}
}
.topNavBar .submenu {
  display: none;
  position: absolute;
  right: 0;
  top: 100%;
  background: white;
  color: #3d4451;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
.topNavBar li.active > .submenu {
  display: block;
  -webkit-animation: submenuSlide 0.3s;
          animation: submenuSlide 0.3s;
}
@-webkit-keyframes submenuSlide {
0% {
    margin-right: 100%;
    /*自己多试试别的属性*/
}
100% {
    margin-right: 0%;
}
}
@keyframes submenuSlide {
0% {
    margin-right: 100%;
    /*自己多试试别的属性*/
}
100% {
    margin-right: 0%;
}
}
.topNavBar .submenu > li {
  white-space: nowrap;
  padding: 5px 10px;
}
.topNavBar .logo {
  font-size: 24px;
  font-family: "Arial Black";
  padding-top: 3px;
  padding-bottom: 4px;
}
.topNavBar .logo .rs {
  color: rgb(252, 110, 146);
}
.topNavBar .logo .card {
  color: #9a9da2;
}
